@import "https://fonts.googleapis.com/css?family=Montserrat:600";
@import "https://use.fontawesome.com/releases/v5.2.0/css/all.css";
html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  font: normal 16px "Montserrat", sans-serif;
  padding: 15px;
  background: -webkit-gradient(linear, left bottom, right top, from(#051937), color-stop(#004d7a), color-stop(#008793), color-stop(#00bf72), to(#a8eb12)) no-repeat;
  background: -webkit-linear-gradient(left bottom, #051937, #004d7a, #008793, #00bf72, #a8eb12) no-repeat;
  background: -o-linear-gradient(left bottom, #051937, #004d7a, #008793, #00bf72, #a8eb12) no-repeat;
  background: linear-gradient(to right top, #051937, #004d7a, #008793, #00bf72, #a8eb12) no-repeat;
  background-attachment: fixed; }

button {
  background: none;
  border: 0;
  outline: 0;
  cursor: pointer; }



